.changes-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  box-sizing: border-box;
  position: relative;
  text-align: center;
}

.changes-page h1 {
  color: white;
  margin-bottom: 30px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.changes-grid-container {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.changes-content {
  display: flex;
  gap: 20px;
  align-items: flex-start;
}

.changes-grid {
  display: inline-block;
  border: 2px solid #000;
  padding: 2px;
  background-color: white;
}

.grid-row {
  display: flex;
}

.grid-row:nth-child(3n) {
  border-bottom: 2px solid #000;
}

.grid-row:last-child {
  border-bottom: none;
}

.changes-cell:nth-child(3n) {
  border-right: 2px solid #000;
}

.changes-cell:last-child {
  border-right: 1px solid #ccc;
}

.changes-controls {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 20px;
}

.changes-controls button {
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.2s ease;
  background-color: rgba(248, 249, 250, 0.1);
  color: white;
  border: 1px solid rgba(222, 226, 230, 0.2);
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
}

.changes-controls button:hover {
  background-color: rgba(233, 236, 239, 0.2);
  border-color: rgba(206, 212, 218, 0.3);
}

.changes-controls button:active {
  transform: translateY(1px);
}

.changes-info {
  padding: 20px;
  background-color: rgba(248, 249, 250, 0.1);
  border-radius: 8px;
  margin-top: 20px;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
}

.change-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
  padding-bottom: 10px;
  border-bottom: 1px solid rgba(222, 226, 230, 0.2);
}

.change-number, .change-type {
  font-size: 1.1em;
  color: white;
  font-weight: bold;
}

.change-description {
  font-family: monospace;
  white-space: pre-wrap;
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  border: 1px solid rgba(222, 226, 230, 0.2);
  color: white;
}

.changes-controls-bottom {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
  padding: 20px 0;
  position: relative;
  z-index: 1;
}

.back-button {
  display: flex;
  align-items: center;
  gap: 5px;
}

.back-buttons {
  position: fixed;
  top: 20px;
  left: 20px;
  display: flex;
  flex-direction: column;
  gap: 15px;
  z-index: 2000;
}

.back-buttons .back-button {
  margin: 0;
  width: 200px;
}

.back-buttons .back-button:last-child {
  margin-bottom: 0;
} 