.control-panel {
  margin-top: 20px;
  display: flex;
  gap: 10px;
  justify-content: center;
}

.control-button {
  padding: 10px 20px;
  font-size: 1em;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.2s ease;
  background-color: rgba(248, 249, 250, 0.1);
  color: white;
  border: 1px solid rgba(222, 226, 230, 0.2);
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
}

.control-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.control-button:hover:not(:disabled) {
  background-color: rgba(233, 236, 239, 0.2);
  border-color: rgba(206, 212, 218, 0.3);
}

.control-button:active {
  transform: scale(0.95);
}

.reset-button, .solve-button, .details-button, .changes-button {
  background-color: rgba(248, 249, 250, 0.1);
  color: white;
}

.reset-button:hover:not(:disabled) {
  background-color: rgba(231, 76, 60, 0.2);
}

.solve-button:hover:not(:disabled) {
  background-color: rgba(46, 204, 113, 0.2);
}

.details-button:hover:not(:disabled) {
  background-color: rgba(52, 152, 219, 0.2);
}

.changes-button:hover:not(:disabled) {
  background-color: rgba(155, 89, 182, 0.2);
} 