.back-button {
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.2s ease;
  background-color: rgba(248, 249, 250, 0.1);
  color: white;
  border: 1px solid rgba(222, 226, 230, 0.2);
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  display: flex;
  align-items: center;
  gap: 8px;
  width: 200px;
  white-space: nowrap;
  font-family: inherit;
}

.back-button:hover {
  background-color: rgba(233, 236, 239, 0.2);
  border-color: rgba(206, 212, 218, 0.3);
}

.back-button:active {
  transform: translateY(1px);
} 