/* Remove the html/body reset since it's handled in index.css */
.results-page {
  max-width: 800px;
  width: 100%;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  position: relative;
  text-align: center;
}

.results-page h1 {
  text-align: center;
  color: white;
  margin-bottom: 30px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.results-page h2 {
  color: white;
  margin-bottom: 20px;
}

.grid-comparison {
  display: flex;
  justify-content: center;
  gap: 40px;
  margin-bottom: 40px;
  flex-wrap: wrap;
}

.grid-comparison > div {
  text-align: center;
}

.solving-metrics {
  background-color: rgba(248, 249, 250, 0.1);
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 40px;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
}

.metrics-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  padding: 10px;
}

.error-message {
  color: #fff;
  background-color: rgba(231, 76, 60, 0.9);
  padding: 10px 20px;
  border-radius: 4px;
  margin: 10px 0;
  text-align: center;
  font-weight: bold;
}

/* Add smooth scrolling to the page */
html {
  scroll-behavior: smooth;
}

/* Make sure the body allows scrolling */
body {
  overflow-y: auto;
}

/* Add some responsive adjustments */
@media (max-width: 640px) {
  .results-page {
    padding: 10px;
    margin: 10px;
  }

  .metrics-grid {
    grid-template-columns: 1fr;
  }

  .grid-comparison {
    flex-direction: column;
    align-items: center;
  }

  .control-button {
    padding: 8px 16px;
    font-size: 14px;
  }
}

.metrics-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
}

.results-controls {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
  padding: 20px 0;
  position: relative;
  z-index: 1;
}

.back-button {
  display: flex;
  align-items: center;
  gap: 5px;
}

.back-buttons {
  position: fixed;
  top: 20px;
  left: 20px;
  display: flex;
  flex-direction: column;
  gap: 15px;
  z-index: 2000;
}

.back-buttons .back-button {
  margin: 0;
  width: 200px;
}

.back-buttons .back-button:last-child {
  margin-bottom: 0;
}