.App {
  text-align: center;
  padding: 20px;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}

.input-page {
  max-width: 800px;
  width: 100%;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
}

.input-page h1 {
  color: white;
  margin-bottom: 30px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.sudoku-grid {
  margin: 0 auto;
  background-color: white;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.sudoku-cell {
  width: 45px;
  height: 45px;
  border: 1px solid #ddd;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  transition: all 0.2s ease;
  outline: none;
  cursor: pointer;
  caret-color: transparent; /* Hides the text cursor */
  background-color: white;  /* Explicit default background */
}

.sudoku-cell:focus {
  background-color: #fff7b2 !important; /* Use !important to ensure focus always shows yellow */
  border-color: #4a90e2;
}

.sudoku-cell:hover:not(:focus) {
  background-color: #f5f5f5;
}

.sudoku-cell.starting-number {
  background-color: #f8f9fa;
  color: #2c3e50;
  font-weight: bold;
}

.grid-row:nth-child(3n) {
  border-bottom: 2px solid #2c3e50;
}

.sudoku-cell:nth-child(3n) {
  border-right: 2px solid #2c3e50;
}

/* Remove the double border on the bottom of the grid */
.grid-row:last-child {
  border-bottom: none;
}

/* Remove the double border on the right of the grid */
.sudoku-cell:last-child {
  border-right: 1px solid #ddd;
} 